import React from "react";
import FormStyled from "./FormStyled";
import { useState } from "react";

const Form: React.FC = () => {
  const timeElapsed = Date.now();
  const today = new Date(timeElapsed);
  const hoje = today.toLocaleDateString();

  const [showElement, setShowElement] = useState(true);
  const showOrHide = () => setShowElement(!showElement);

  const [showElement2, setShowElement2] = useState(true);
  const showOrHide2 = () => setShowElement2(!showElement2);

  return (
    <FormStyled>
      <section>
        <div className="div-form mt-4">
          <div className="posicao-form">
            <form action="">
              <h1>Número de cadastro da CRM: 01245</h1>
              <h3>Responsável:</h3>
              <div className="div-usuario">
                <div className="foto-usuario"></div>
                <p className="texto-usuario">
                  Nome: João Silva
                  <br />
                  Matrícula: 095021
                  <br />
                  Setor: Mercantil
                  <br />
                  Telefone: (99)99999-9999
                  <br />
                  E-mail: joao.silva@quero-quero.com.br
                </p>
              </div>
              <label htmlFor="participantes">Setores participantes:</label>
              <br />
              <input
                type="search"
                name="participantes"
                id=""
                className="input-participantes"
              />
              <div className="d-flex">
                <div className="div-setor d-flex align-items-center">
                  TI
                  <button className="botao-x" disabled>
                    <i className="fa-solid fa-times-circle" />
                  </button>
                </div>
                {showElement ? (
                  <div className="div-setor ms-2 d-flex align-items-center">
                    Contabilidade
                    <button className="botao-x" onClick={showOrHide}>
                      <i className="fa-solid fa-times-circle" />
                    </button>
                  </div>
                ) : null}
                {showElement2 ? (
                  <div className="div-setor ms-2 d-flex align-items-center">
                    Lorem
                    <button className="botao-x" onClick={showOrHide2}>
                      <i className="fa-solid fa-times-circle" />
                    </button>
                  </div>
                ) : null}
              </div>
              <label htmlFor="descricao" className="pt-3">
                Descrição:
              </label>
              <input
                type="text"
                name="descricao"
                id=""
                className="input-descricao"
              />
              <label htmlFor="objetivo">Objetivo:</label>
              <input
                type="text"
                name="objetivo"
                id=""
                className="input-objetivo"
              />
              <label htmlFor="justificativa">Justificativa:</label>
              <input
                type="text"
                name="justificativa"
                id=""
                className="input-justificativa"
              />
              <div className="div-versao-data">
                <div className="div-usuario">
                  <p>Versão:</p>
                  <p className="ps-2">v1</p>
                </div>
                <div className="div-usuario">
                  <p>Data criação:</p>
                  <p className="ps-2">{hoje}</p>
                </div>
              </div>
              <label htmlFor="documentos">Documentos:</label>
              <input
                type="text"
                name="documentos"
                id=""
                className="input-documentos"
              />
              <br />
              <button type="button" className="botao-anexar">
                <i className="fa-solid fa-upload"></i>
                Anexar documentos
              </button>
              <div className="div-salvar">
                <button type="submit" className="botao-salvar">
                  SALVAR
                </button>
              </div>
            </form>
          </div>
        </div>
      </section>
    </FormStyled>
  );
};

export default Form;
