import React from "react";
import UserStyled from "./UserStyled";

const User: React.FC = () => {
  return (
    <UserStyled>
      <section>
        <div className="div-form mt-4 d-flex justify-content-center align-items-center">
          <form className="d-flex flex-column align-items-center">
            <div className="avatar"></div>
            <button>Editar foto</button>
            <div className="mt-3 d-flex flex-column align-items-start campos">
              <div className="d-flex align-items-end">
                <h6>Nome:</h6>
                <input
                  type="text"
                  className="ps-1 ms-2"
                  value="João Silva"
                  disabled
                />
              </div>
              <div className="d-flex mt-2 align-items-end">
                <h6>Matrícula:</h6>
                <input
                  type="text"
                  className="ps-1 ms-2"
                  value="095021"
                  disabled
                />
              </div>
              <div className="d-flex mt-2 align-items-end">
                <h6>Telefone:</h6>
                <input
                  type="text"
                  className="ps-1 ms-2"
                  value="(99)99999-9999"
                />
              </div>
              <div className="d-flex mt-2 align-items-end">
                <h6>Setor:</h6>
                <select className="ps-1 ms-2" name="setor" id="setor">
                  <option value="mercantil" selected>
                    Mercantil
                  </option>
                  <option value="ti">TI</option>
                  <option value="contabilidade">Contabilidade</option>
                  <option value="lorem">Lorem</option>
                  <option value="ipsum">Ipsum</option>
                </select>
              </div>
              <div className="d-flex mt-2 align-items-end">
                <h6>E-mail:</h6>
                <input
                  type="email"
                  className="ps-1 ms-2"
                  value="joao.silva@quero-quero.com.br"
                  disabled
                />
              </div>
              <div className="d-flex mt-2 align-items-end">
                <h6>Senha:</h6>
                <input
                  type="password"
                  className="ps-1 ms-2"
                  value="testesenha"
                />
              </div>
            </div>
            <button className="mt-4 botao-salvar">SALVAR</button>
          </form>
        </div>
      </section>
    </UserStyled>
  );
};

export default User;
