import * as React from "react";
import LoginStyled from "./LoginStyled";
import logo from "../../assets/logo-quero-quero.png";
import { useNavigate } from "react-router-dom";

const Login: React.FC = () => {
  const navigate = useNavigate();

  function entrar() {
    navigate("/home");
  }

  return (
    <LoginStyled>
      <body className="fundo" id="background">
        <div className="main">
          <input type="checkbox" id="chk" aria-hidden="true" />

          <div className="signup d-flex justify-content-center">
            <form id="formLogin">
              <div className="d-flex flex-column justify-content-center align-items-center">
                <img src={logo} alt="" className="logo-quero-quero mt-5" />
                <label
                  className="titulo-login mt-3 mb-5"
                  htmlFor="chk"
                  aria-hidden="true"
                >
                  Login CRM
                </label>
              </div>
              <div className="login__row input-group pt-0 d-flex justify-content-center">
                <span
                  className="input-group-text shadow basic-addon1"
                  id="spanLoginUser"
                >
                  <i className="fa-solid fa-user"></i>
                </span>
                <input
                  type="email"
                  className="login__input shadow"
                  placeholder="E-mail"
                  id="inputLoginUser"
                />
              </div>
              <div className="login__row input-group pt-0 mt-3 d-flex justify-content-center">
                <span
                  className="input-group-text shadow basic-addon1"
                  id="spanLoginPass"
                >
                  <i className="fa-solid fa-lock"></i>
                </span>
                <input
                  type="password"
                  className="login__input shadow"
                  placeholder="Senha"
                  id="inputLoginPass"
                />
              </div>
              <div className="d-flex justify-content-center">
                <button className="login__submit mt-4" onClick={entrar}>
                  <span className="button_top">ENTRAR</span>
                </button>
              </div>
            </form>
          </div>

          <div className="login d-flex justify-content-center" id="cadastro">
            <form id="formCadastro">
              <div className="d-flex justify-content-center">
                <label htmlFor="chk" aria-hidden="true">
                  Criar conta
                </label>
              </div>
              <div className="login__row input-group pt-0 d-flex justify-content-center">
                <span
                  className="input-group-text shadow basic-addon1"
                  id="spanCadastroNome"
                >
                  <i className="fa-regular fa-user"></i>
                </span>
                <input
                  type="text"
                  className="login__input shadow"
                  placeholder="Nome"
                  id="inputCadastroNome"
                  data-bs-toggle="popover"
                  data-bs-container="body"
                  data-bs-trigger="focus"
                  data-bs-placement="top"
                  data-bs-content="Mínimo de 3 caracteres"
                />
              </div>
              <div className="login__row input-group pt-0 mt-3 d-flex justify-content-center">
                <span
                  className="input-group-text shadow basic-addon1"
                  id="spanCadastroUser"
                >
                  <i className="fa-solid fa-user"></i>
                </span>
                <input
                  type="email"
                  className="login__input shadow"
                  placeholder="E-mail"
                  id="inputCadastroUser"
                  data-bs-toggle="popover"
                  data-bs-container="body"
                  data-bs-trigger="focus"
                  data-bs-placement="top"
                  data-bs-content="Mínimo de 10 caracteres"
                />
              </div>
              <div className="login__row input-group pt-0 mt-3 d-flex justify-content-center">
                <span
                  className="input-group-text shadow basic-addon1"
                  id="spanCadastroPass"
                >
                  <i className="fa-solid fa-lock"></i>
                </span>
                <input
                  type="password"
                  className="login__input shadow"
                  placeholder="Senha"
                  id="inputCadastroPass"
                  data-bs-toggle="popover"
                  data-bs-container="body"
                  data-bs-trigger="focus"
                  data-bs-placement="top"
                  data-bs-content="Deve conter: minúscula, maiúscula, número e caracter especial"
                />
              </div>
              <div className="d-flex justify-content-center">
                <button className="login__submit mt-5">
                  <span className="button_top">CADASTRAR</span>
                </button>
              </div>
            </form>
          </div>
        </div>

        <div
          className="toast text-bg-primary"
          role="alert"
          aria-live="assertive"
          aria-atomic="true"
          id="toastCadastro"
          data-bs-autohide="false"
        >
          <div className="toast-body">
            <p>Conta criada com sucesso!</p>
            <p>Deseja ir para a página de login?</p>
            <div className="mt-2 pt-2 border-top">
              <button
                type="button"
                className="btn btn-sm bg-warning"
                data-bs-dismiss="toast"
                id="toastCadastroSim"
              >
                Sim
              </button>
              <button
                type="button"
                className="btn btn-sm bg-info"
                data-bs-dismiss="toast"
              >
                Não
              </button>
            </div>
          </div>
        </div>

        <div
          className="toast align-items-center text-bg-primary border-0"
          role="alert"
          aria-live="assertive"
          aria-atomic="true"
          data-bs-autohide="false"
          id="toastDuplicidade"
        >
          <div className="d-flex">
            <div className="toast-body">
              E-mail já tinha sido cadastrado. Redirecionando para a página de
              login.
            </div>
            <button
              type="button"
              className="btn-close btn-close-white me-2 m-auto"
              data-bs-dismiss="toast"
              aria-label="Close"
              id="toastDuplicidadeClose"
            ></button>
          </div>
        </div>

        <div
          className="toast align-items-center text-bg-primary border-0"
          role="alert"
          aria-live="assertive"
          aria-atomic="true"
          data-bs-autohide="false"
          id="toastCamposIncorretos"
        >
          <div className="d-flex">
            <div className="toast-body">
              Campos incorretos! Por favor verifique se você preencheu todos os
              campos corretamente.
            </div>
            <button
              type="button"
              className="btn-close btn-close-white me-2 m-auto"
              data-bs-dismiss="toast"
              aria-label="Close"
            ></button>
          </div>
        </div>

        <div
          className="toast align-items-center text-bg-primary border-0"
          role="alert"
          aria-live="assertive"
          aria-atomic="true"
          data-bs-autohide="false"
          id="toastEmailNovo"
        >
          <div className="d-flex">
            <div className="toast-body">
              Você não está cadastrado. Indo para o cadastro.
            </div>
            <button
              type="button"
              className="btn-close btn-close-white me-2 m-auto"
              data-bs-dismiss="toast"
              aria-label="Close"
              id="toastEmailNovoClose"
            ></button>
          </div>
        </div>

        <div
          className="toast align-items-center text-bg-primary border-0"
          role="alert"
          aria-live="assertive"
          aria-atomic="true"
          data-bs-autohide="false"
          id="toastEmailIncorreto"
        >
          <div className="d-flex">
            <div className="toast-body">Senha incorreta</div>
            <button
              type="button"
              className="btn-close btn-close-white me-2 m-auto"
              data-bs-dismiss="toast"
              aria-label="Close"
              id="toastEmailIncorretoClose"
            ></button>
          </div>
        </div>

        <div
          className="toast align-items-center text-bg-primary border-0"
          role="alert"
          aria-live="assertive"
          aria-atomic="true"
          data-bs-autohide="false"
          id="toastLogin"
        >
          <div className="d-flex">
            <div className="toast-body">
              Você já está logado como <span id="spanNomeLogado"></span>!
            </div>
            <button
              type="button"
              className="btn-close btn-close-white me-2 m-auto"
              data-bs-dismiss="toast"
              aria-label="Close"
              id="toastLoginClose"
            ></button>
          </div>
        </div>
      </body>
    </LoginStyled>
  );
};

export default Login;
